.detailbox{
    position: fixed;
    width: 750rem;
    max-height: 1100rem;
    background-color: white;
    bottom: 0;
    padding: 0 30rem 150rem;
    box-sizing: border-box;
    border-radius: 60rem 60rem 0 0;
}
.header{
    padding: 20rem 0 0;
    display: flex;
    justify-content: space-between;
    cursor:context-menu
}
.right{
    color: #666;
    z-index: 2;
}
.Backdrop{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.229);
    z-index: -9999;
}
.main{
    max-height: 1100rem;
    overflow: auto;
    padding-bottom: 120rem;
    box-sizing: border-box;
}