.cardBox{
    height: 100rem;
    width: 720rem;
    margin: 0 auto;
    background-color: #3A3A3A;
    position: fixed;
    bottom: 10rem;
    right: 0;
    left: 0;
    box-sizing: border-box;
    border-radius: 50rem;
    display: flex;
    z-index: 9999;
}
.main{
    flex: auto;
    display: flex;
}
.left{
    width: 110rem;
    height: 100%;
    position: relative;
}
.img{
    width: 100%;
}
.img img{
    width: 110rem;
    height: 140rem;
    position: absolute;
    bottom: -10rem;
}
.counter{
    width: 48rem;
    height: 48rem;
    background-color: #dc0416;
    border-radius: 50%;
    position: absolute;
    right: -20rem;
    top: -20rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 32rem;
    font-weight: 700;
}
.price, .noshop{
    height: 100%;
    padding-left: 30rem;
    font-size: 45rem;
    display: flex;
    align-items: center;
    color: white;
    font-weight: 600;
    cursor:context-menu
}
.price::before{
    content: '￥';
    font-size: 30rem;
    padding-top: 5rem;
}
.noshop{
    color: #aaa;
}
.gogo{
    width: 200rem;
    background-color: #F2C308;
    border-radius: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36rem;
    border: none;
}
.donot{
    background-color: #777;
    color: #eee;
}