.cardBox{
    height: 100rem;
    width: 720rem;
    margin: 0 auto;
    background-color: #3A3A3A;
    position: fixed;
    bottom: 10rem;
    right: 0;
    left: 0;
    box-sizing: border-box;
    border-radius: 50rem;
    display: flex;
    z-index: 9999;
    justify-content: space-between;
}
.price{
    height: 100%;
    padding-left: 50rem;
    font-size: 45rem;
    display: flex;
    align-items: center;
    color: white;
    font-weight: 600;
    cursor:context-menu
}
.price::before{
    content: '合计￥';
    font-size: 28rem;
    padding-top: 10rem;
}
.gogo{
    width: 200rem;
    background-color: #F2C308;
    border-radius: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36rem;
    border: none;
}