.box{
    display: flex;
    padding: 0 10rem;
}
.image{
    width: 160rem;
    height: 160rem;
}
.image img{
    width: 100%;
    height: 100%;
}
.right{
    box-sizing: border-box;
    padding-left: 20rem;
    flex: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.title{
    font-weight: 600;
}
.main{
    display: flex;
    justify-content: space-between;
}
.price{
    font-size: 34rem;
    font-weight: 600;
}
.price::before{
    content: "￥";
    font-size: 20rem;
}