.box{
    position: absolute;
    z-index: 9999;
    background-color: rgb(227, 227, 227);
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.back{
    color: #444;
    padding: 0 15rem;
    box-sizing: border-box;
    font-size: 40rem;
}
.buymain{
    width: 680rem;
    background-color: white;
    border-radius: 20rem;
    margin: 5rem auto 0;
    box-sizing: border-box;
    padding: 0 20rem;
}
.header{
    height: 30rem;
    padding: 20rem 0;
    border-bottom: 1px solid #ccc;
}
.footer{
    padding: 38rem 0;
    display: flex;
    justify-content: flex-end;
    border-top: 1px dashed #ccc;
    position: relative;
}
.footer::after, .footer::before{
    content: '';
    width: 30rem;
    height: 30rem;
    background-color: rgb(227, 227, 227);
    border-radius: 50%;
    position: absolute;
    left: -35rem;
    top: -15rem;
}
.footer::after{
    left: auto;
    right: -35rem;
}
.sptext{
    font-size: 38rem;
    font-weight: 700;
}
.sptext::before{
    content: "合计￥";
    font-size: 24rem;
    font-weight: normal;
}