.box{
    border-bottom: 1px solid #ccc;
    display: flex;
    box-sizing: border-box;
    padding: 10rem 10rem 10rem 0;
}
.img{
    width: 220rem;
    height: 220rem;
}
img{
    width: 220rem;
    height: 220rem;
}
.title{
    font-size: 32rem;
    font-weight: 600;
    margin-bottom: 2rem;
}
.itembody{
    width: 530rem;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 0 20rem;
    justify-content: space-evenly;
}
.bottom{
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
}
.price{
    font-size: 36rem;
    font-weight: 600;
}
.price::before{
    content: "￥";
    font-size: 26rem;
}