.confirm{
    width: 600rem;
    height: 250rem;
    background-color: white;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    border-radius: 40rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}
.cancel, .ok{
    border: 1px solid #ccc;
    width: 160rem;
    height: 50rem;
    border-radius: 40rem;
    background-color: white;
    font-size: 24rem;
}
.ok{
    border: none;
    background-color: #F2C308;
    margin-left: 60rem;
}