.counter{
    display: flex;
    box-sizing: border-box;
}
.counter>button{
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background-color: #F0C308;
    width: 35rem;
    height: 35rem;
    border-radius: 50%;
    font-size: 27rem;
}
.counter .botleft{
    border: 1px solid black;
    background-color: white;
}
.botmain{
    margin: 0 15rem;
}