.searchbox{
    height: 100rem;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
}
.main{
    position: relative;
    display: flex;
    align-items: center;
}
.searchInput{
    width: 700rem;
    height: 70rem;
    font-size: 28rem;
    border: none;
    outline: none;
    border-radius: 50rem;
    box-sizing: border-box;
    padding-left: 50rem;
    background-color: #f2f2f2;
}
.searchIcon{
    position: absolute;
    left: 12rem;
    color: #666;
    font-size: 30rem;
}